<script>
import Vue from 'vue';
import validate from '../common/validate/validateCheck.js';
const f7Input = Vue.options.components['f7-input'].extendOptions;
export default {
  meta: {
    global: true,
  },
  name: 'eb-input',
  extends: f7Input,
  mixins: [ validate ],
  mounted() {
    this.$nextTick(() => {
      this.checkEmptyState();
    });
  },
  methods: {
    onValidateError(error) {
      const input = this._findText();
      input[0].setCustomValidity(error);
      input[0].ebCustomError = error;
      this.$f7.input.validate(input);
      this.checkEmptyState();
    },
    onInput(event) {
      const input = this._findText();
      input[0].ebCustomError = null;
      this.$emit('input', event.target.value);
      this.clearValidateError();
    },
    onChange(event) {
      this.$emit('change', event.target.value);
    },
    onInputClear(event) {
      this.$emit('input:clear', event.target.value);
    },
    checkEmptyState() {
      this.$f7.input.checkEmptyState(this._findText());
    },
    _findText() {
      const tag = this.type === 'textarea' ? 'textarea' : 'input';
      return this.$$(this.$el).find(tag);
    },
  },
};

</script>
